import {
  ChangeDetectionStrategy,
  Component,
  ElementRef,
  Input,
  OnChanges,
  OnDestroy,
  OnInit,
  SimpleChanges,
  TemplateRef,
  ViewEncapsulation
} from '@angular/core';

import { InputBoolean, NzDirectionVHType, NzSizeLDSType, NzUpdateHostClassService } from 'ng-zorro-antd/core';

import { BehaviorSubject, Observable } from 'rxjs';
import { NzListGrid } from 'ng-zorro-antd';

export type BpsListType = 'variation1' | 'variation2' | 'variation3' | 'variation4' | 'variation5' | 'variation6';

@Component({
  selector: 'bps-list, [bps-list]',
  exportAs: 'bpsList',
  templateUrl: './bps-list.component.html',
  providers: [NzUpdateHostClassService],
  preserveWhitespaces: false,
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush,
  styles: [
    `
      bps-list,
      bps-list nz-spin {
        display: block;
      }
    `
  ],
  styleUrls: ['./bps-list.component.css']
})
export class BpsListComponent implements OnInit, OnChanges, OnDestroy {
  // #region fields
  // tslint:disable-next-line:no-any
  @Input() bpsDataSource: any[];

  @Input() @InputBoolean() bpsBordered = false;

  @Input() @InputBoolean() bpsDisabled = false;

  @Input() bpsGrid: NzListGrid;

  @Input() bpsListType: BpsListType = 'variation1';

  @Input() bpsHeader: string | TemplateRef<void>;

  @Input() bpsFooter: string | TemplateRef<void>;

  @Input() bpsItemLayout: NzDirectionVHType = 'horizontal';

  @Input() bpsRenderItem: TemplateRef<void>;

  @Input() @InputBoolean() bpsLoading = false;

  @Input() bpsLoadMore: TemplateRef<void>;

  @Input() bpsPagination: TemplateRef<void>;

  @Input() bpsSize: NzSizeLDSType = 'default';

  @Input() @InputBoolean() bpsSplit = true;

  @Input() bpsNoResult: string | TemplateRef<void>;

  // #endregion

  // #region styles

  private prefixCls = 'ant-list';

  private _setClassMap(): void {
    const classMap = {
      ['bps-cmacs-custom-scrollbar']: true,
      [this.prefixCls]: true,
      [`bps-list-type-${this.bpsListType}`]: true,
      [`${this.prefixCls}-vertical`]: this.bpsItemLayout === 'vertical',
      [`${this.prefixCls}-lg`]: this.bpsSize === 'large',
      [`${this.prefixCls}-sm`]: this.bpsSize === 'small',
      [`${this.prefixCls}-split`]: this.bpsSplit,
      [`${this.prefixCls}-bordered`]: this.bpsBordered,
      [`${this.prefixCls}-loading`]: this.bpsLoading,
      [`${this.prefixCls}-grid`]: this.bpsGrid,
      [`${this.prefixCls}-something-after-last-item`]: !!(this.bpsLoadMore || this.bpsPagination || this.bpsFooter)
    };
    this.updateHostClassService.updateHostClass(this.el.nativeElement, classMap);
  }

  // #endregion

  private itemLayoutNotifySource = new BehaviorSubject<NzDirectionVHType>(this.bpsItemLayout);

  get itemLayoutNotify$(): Observable<NzDirectionVHType> {
    return this.itemLayoutNotifySource.asObservable();
  }

  constructor(private el: ElementRef, private updateHostClassService: NzUpdateHostClassService) {}

  ngOnInit(): void {
    this._setClassMap();
  }

  ngOnChanges(changes: SimpleChanges): void {
    this._setClassMap();
    if (changes.bpsItemLayout) {
      this.itemLayoutNotifySource.next(this.bpsItemLayout);
    }
  }

  ngOnDestroy(): void {
    this.itemLayoutNotifySource.unsubscribe();
  }
}
