import { Directive, ElementRef, Input, Renderer2 } from '@angular/core';
import { InputBoolean, NzSizeLDSType } from 'ng-zorro-antd/core';

@Directive({
  selector: '[bps-input]',
  exportAs: 'bpsInput',
  host: {
    '[class.ant-input-disabled]': 'disabled',
    '[class.ant-input-lg]': `bpsSize === 'large'`,
    '[class.ant-input-sm]': `bpsSize === 'small'`,
    '[class.bps-input-centered]': `centered`,
    '[class.bps-input-opened]': `opened`,
    '[class.bps-custom-disabled]': `bpsDisabled`,
  }
})
export class BpsInputDirective {
  @Input() bpsSize: NzSizeLDSType = 'default';
  @Input() @InputBoolean() disabled = false;
  @Input() @InputBoolean() centered = false;
  @Input() @InputBoolean() opened = false;
  @Input() @InputBoolean() bpsDisabled = false;

  constructor(renderer: Renderer2, elementRef: ElementRef) {
    renderer.addClass(elementRef.nativeElement, 'ant-input');
  }
}
