import {
  AfterViewInit,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ContentChildren,
  ElementRef,
  HostBinding,
  Input,
  OnDestroy,
  QueryList,
  Renderer2,
  TemplateRef,
  ViewEncapsulation,
  EventEmitter,
  Output
} from '@angular/core';

import { InputBoolean, NzDirectionVHType } from 'ng-zorro-antd/core';
import { Subscription } from 'rxjs';
import { BpsListItemMetaComponent } from './bps-list-item-meta.component';
import { BpsListComponent } from './bps-list.component';

@Component({
  selector: 'bps-list-item, [bps-list-item]',
  exportAs: 'bpsListItem',
  templateUrl: './bps-list-item.component.html',
  preserveWhitespaces: false,
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush,
  host: {
    '[class.bps-delete-icon-hovered]': '_onDeleteHover'
  }
})
export class BpsListItemComponent implements OnDestroy, AfterViewInit {

  _onDeleteHover = false;

  @ContentChildren(BpsListItemMetaComponent) metas!: QueryList<BpsListItemMetaComponent>;
  @Input() bpsActions: Array<TemplateRef<void>> = [];
  @Input() bpsContent: string | TemplateRef<void>;
  @Input() bpsExtra: TemplateRef<void>;
  @Input() @InputBoolean() @HostBinding('class.ant-list-item-no-flex') bpsNoFlex: boolean = false;
  @Input() @InputBoolean() bpsDelete = false;

  @Output() ondelete: EventEmitter<any> = new EventEmitter<any>();
  @Output() ondeletehover: EventEmitter<any> = new EventEmitter<any>();
  @Output() hover: EventEmitter<any> = new EventEmitter<any>();

  private itemLayout: NzDirectionVHType;
  private itemLayout$: Subscription;

  get isVerticalAndExtra(): boolean {
    return this.itemLayout === 'vertical' && !!this.bpsExtra;
  }

  onDeleteHover(event) {
    this._onDeleteHover = event;
    this.ondeletehover.emit(event);
  }

  onHover(event) {
    this.hover.emit(event);
  }

  onDelete() {
    this.ondelete.emit();
  }

  constructor(
    elementRef: ElementRef,
    renderer: Renderer2,
    private parentComp: BpsListComponent,
    private cdr: ChangeDetectorRef
  ) {
    renderer.addClass(elementRef.nativeElement, 'ant-list-item');
  }

  ngAfterViewInit(): void {
    this.itemLayout$ = this.parentComp.itemLayoutNotify$.subscribe(val => {
      this.itemLayout = val;
      this.cdr.detectChanges();
    });
  }

  ngOnDestroy(): void {
    if (this.itemLayout$) {
      this.itemLayout$.unsubscribe();
    }
  }
}
