
import { ChangeDetectionStrategy, Component, ElementRef, Input, OnInit, ViewEncapsulation } from '@angular/core';
import { NzSizeLDSType, NzUpdateHostClassService } from 'ng-zorro-antd/core';

@Component({
  selector: 'bps-button-group',
  exportAs: 'bpsButtonGroup',
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  preserveWhitespaces: false,
  providers: [NzUpdateHostClassService],
  templateUrl: './bps-button-group.component.html'
})
export class BpsButtonGroupComponent implements OnInit {
  private _size: NzSizeLDSType;
  isInDropdown = false;

  @Input()
  get bpsSize(): NzSizeLDSType {
    return this._size;
  }

  set bpsSize(value: NzSizeLDSType) {
    this._size = value;
    this.setClassMap();
  }

  constructor(private nzUpdateHostClassService: NzUpdateHostClassService, private elementRef: ElementRef) {}

  setClassMap(): void {
    const prefixCls = 'ant-btn-group';
    const classMap = {
      [prefixCls]: true,
      [`ant-dropdown-button`]: this.isInDropdown,
      [`${prefixCls}-lg`]: this.bpsSize === 'large',
      [`${prefixCls}-sm`]: this.bpsSize === 'small'
    };
    this.nzUpdateHostClassService.updateHostClass(this.elementRef.nativeElement, classMap);
  }

  ngOnInit(): void {
    this.setClassMap();
  }
}
