import {
  ChangeDetectionStrategy,
  Component,
  ElementRef,
  EventEmitter,
  Output,
  Renderer2,
  ViewEncapsulation
} from '@angular/core';

import { BpsCheckboxComponent } from './bps-checkbox.component';

@Component({
  selector: 'bps-checkbox-wrapper',
  exportAs: 'bpsCheckboxWrapper',
  preserveWhitespaces: false,
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  templateUrl: './bps-checkbox-wrapper.component.html'
})
export class BpsCheckboxWrapperComponent {
  @Output() readonly bpsOnChange = new EventEmitter<string[]>();
  private checkboxList: BpsCheckboxComponent[] = [];

  addCheckbox(value: BpsCheckboxComponent): void {
    this.checkboxList.push(value);
  }

  removeCheckbox(value: BpsCheckboxComponent): void {
    this.checkboxList.splice(this.checkboxList.indexOf(value), 1);
  }

  outputValue(): string[] {
    const checkedList = this.checkboxList.filter(item => item.bpsChecked);
    return checkedList.map(item => item.bpsValue);
  }

  onChange(): void {
    this.bpsOnChange.emit(this.outputValue());
  }

  constructor(renderer: Renderer2, elementRef: ElementRef) {
    renderer.addClass(elementRef.nativeElement, 'ant-checkbox-group');
  }
}
