import {
  ChangeDetectorRef,
  Component,
  OnDestroy,
  OnInit,
  ChangeDetectionStrategy,
  Input,
  EventEmitter,
  Output,
  AfterViewInit,
} from '@angular/core';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { InputBoolean } from 'ng-zorro-antd';

@Component({
  // tslint:disable-next-line: component-selector
  selector: 'bps-configuration-selector',
  exportAs: 'bpsConfigurationSelector',
  templateUrl: './bps-configuration-selector.component.html',
  styleUrls: ['./bps-configuration-selector.component.css'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
// tslint:disable-next-line no-any
export class BpsConfigurationSelectorComponent<T = any> implements OnInit, OnDestroy, AfterViewInit {
  
  private destroy$ = new Subject<void>();
  currentDisplayedDataIndex = null;
  _data = [];
  _currentDisplayedData = null;

  @Input()
  set currentDisplayedData(value) {
    this._currentDisplayedData = value;
    this.currentDisplayedDataIndex = this.getCurrentElementIndex();
  };

  @Input() propertyName = '';
  @Input() @InputBoolean() disabled = false;
  @Output() currentDisplayedDataChange: EventEmitter<any> = new EventEmitter<any>();
  @Input()
  set data(data) {
    this._data = data;
    if (this._data.length) {
      if (!this.currentDisplayedData) {
        this._currentDisplayedData = this._data[0];
        this.currentDisplayedDataIndex = 0;
      } else {
        this.currentDisplayedDataIndex = this.getCurrentElementIndex();
      }
    }
    this.cdr.detectChanges();
  }

  constructor(
    private cdr: ChangeDetectorRef
  ) {  }

  ngOnInit(): void {
  }

  ngAfterViewInit() {
    this.cdr.detectChanges();
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
  }

  getCurrentElementIndex() {
    for (let i = 0; i < this._data.length; i++) {
      if (this._data[i] === this._currentDisplayedData) {
        return i;
      }
    }
  }

  getPrevElement() {
    if (this.currentDisplayedDataIndex && !this.disabled) {
      this.currentDisplayedDataIndex--;
      this._currentDisplayedData = this._data[this.currentDisplayedDataIndex];
      this.currentDisplayedDataChange.emit(this._currentDisplayedData);
    }
  }

  getNextElement() {
    if (this.currentDisplayedDataIndex + 1 < this._data.length && !this.disabled) {
      this.currentDisplayedDataIndex++;
      this._currentDisplayedData = this._data[this.currentDisplayedDataIndex];
      this.currentDisplayedDataChange.emit(this._currentDisplayedData);
    }
  }

}
