import {
  ChangeDetectionStrategy,
  Component,
  Input,
  OnChanges,
  TemplateRef,
  ViewChild,
  ViewEncapsulation
} from '@angular/core';

import { InputBoolean } from 'ng-zorro-antd/core';
import { Subject } from 'rxjs';

@Component({
  selector: 'bps-option',
  exportAs: 'bpsOption',
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush,
  templateUrl: './bps-option.component.html'
})
export class BpsOptionComponent implements OnChanges {
  changes = new Subject();
  @ViewChild(TemplateRef, { static: false }) template: TemplateRef<void>;
  @Input() bpsLabel: string;
  // tslint:disable-next-line:no-any
  @Input() bpsValue: any;
  @Input() @InputBoolean() bpsDisabled = false;
  @Input() @InputBoolean() bpsHide = false;
  @Input() @InputBoolean() bpsCustomContent = false;

  ngOnChanges(): void {
    this.changes.next();
  }
}
