import {
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ContentChild,
  Host,
  Input,
  Optional,
  TemplateRef,
  ViewEncapsulation
} from '@angular/core';

import { zoomBigMotion, NzNoAnimationDirective, NzTSType } from 'ng-zorro-antd/core';
import { NzTooltipBaseComponentLegacy, NzToolTipComponent } from 'ng-zorro-antd/tooltip';
import { BpsPopoverType } from './bps-popover.directive';

@Component({
  selector: 'bps-popover',
  exportAs: 'bpsPopoverComponent',
  animations: [zoomBigMotion],
  templateUrl: './bps-popover.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  providers: [
    {
      provide: NzTooltipBaseComponentLegacy,
      useExisting: BpsPopoverComponent
    }
  ],
  preserveWhitespaces: false,
  styles: [
    `
      .ant-popover {
        position: relative;
      }
    `
  ],
  styleUrls: ['bps-popover.component.css']
})
export class BpsPopoverComponent extends NzToolTipComponent {
  _prefix = 'ant-popover-placement';

  @Input() bpsPopoverType: BpsPopoverType = 'variation_1';

  @Input() bpsTitle: NzTSType;
  @ContentChild('neverUsedTemplate', { static: true }) bpsTitleTemplate: TemplateRef<void>;

  @Input() bpsContent: NzTSType;
  @ContentChild('nzTemplate', { static: true }) bpsContentTemplate: TemplateRef<void>;

  constructor(cdr: ChangeDetectorRef, @Host() @Optional() public noAnimation?: NzNoAnimationDirective) {
    super(cdr, noAnimation);
  }
}
