import {
  AfterContentInit,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ElementRef,
  Host,
  Injector,
  Optional,
  Renderer2,
  Self,
  TemplateRef,
  ViewChild,
  ViewContainerRef,
  ViewEncapsulation
} from '@angular/core';
import {
  slideMotion,
  NzDropdownHigherOrderServiceToken,
  NzMenuBaseService,
  NzNoAnimationDirective
} from 'ng-zorro-antd/core';

import { Subject } from 'rxjs';
import { NzMenuDropdownService } from 'ng-zorro-antd';

export type NzPlacementType = 'bottomLeft' | 'bottomCenter' | 'bottomRight' | 'topLeft' | 'topCenter' | 'topRight' | 'rightTop';

export function dropdownMenuServiceFactory(injector: Injector): NzMenuBaseService {
  return injector.get(NzMenuDropdownService);
}

@Component({
  selector: `bps-dropdown-menu`,
  templateUrl: './bps-dropdown-menu.component.html',
  exportAs: `bpsDropdownMenu`,
  animations: [slideMotion],
  styleUrls: ['./bps-dropdown-menu.component.css'],
  providers: [
    NzMenuDropdownService,
    {
      provide: NzDropdownHigherOrderServiceToken,
      useFactory: dropdownMenuServiceFactory,
      deps: [[new Self(), Injector]]
    }
  ],
  styles: [
    `
      :root .ant-dropdown.nz-dropdown {
        top: 0;
        left: 0;
        position: relative;
        width: 100%;
        margin-top: 4px;
        margin-bottom: 4px;
      }
    `
  ],
  preserveWhitespaces: false,
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class BpsDropdownMenuComponent implements AfterContentInit {
  open = false;
  triggerWidth = 0;
  dropDownPosition: 'top' | 'center' | 'bottom' = 'bottom';
  visible$ = new Subject<boolean>();
  bpsTrigger: 'click' | 'hover' = 'hover';
  bpsPlacement: NzPlacementType = 'bottomLeft';
  bpsOverlayClassName = '';
  bpsOverlayStyle: { [key: string]: string } = {};
  bpsTableFilter = false;
  // tslint:disable-next-line:no-any
  @ViewChild(TemplateRef, { static: true }) templateRef: TemplateRef<any>;

  setVisibleStateWhen(visible: boolean, trigger: 'click' | 'hover' | 'all' = 'all'): void {
    if (this.bpsTrigger === trigger || trigger === 'all') {
      this.visible$.next(visible);
    }
  }

  setValue<T extends keyof BpsDropdownMenuComponent>(key: T, value: this[T]): void {
    this[key] = value;
    this.cdr.markForCheck();
  }

  constructor(
    private cdr: ChangeDetectorRef,
    private elementRef: ElementRef,
    private renderer: Renderer2,
    public viewContainerRef: ViewContainerRef,
    public nzMenuDropdownService: NzMenuDropdownService,
    @Host() @Optional() public noAnimation?: NzNoAnimationDirective
  ) {}

  ngAfterContentInit(): void {
    this.renderer.removeChild(this.renderer.parentNode(this.elementRef.nativeElement), this.elementRef.nativeElement);
  }
}
