import {
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ElementRef,
  EventEmitter,
  Host,
  Input,
  OnDestroy,
  OnInit,
  Output,
  Renderer2,
  TemplateRef,
  ViewEncapsulation
} from '@angular/core';

import { collapseMotion, InputBoolean, NzConfigService, WithConfig } from 'ng-zorro-antd/core';

import { BpsCollapseComponent } from './bps-collapse.component';

const NZ_CONFIG_COMPONENT_NAME = 'collapsePanel';

@Component({
  selector: 'bps-collapse-panel',
  exportAs: 'bpsCollapsePanel',
  templateUrl: './bps-collapse-panel.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  animations: [collapseMotion],
  styles: [
    `
      bps-collapse-panel {
        display: block;
      }
    `
  ],
  styleUrls: ['./bps-collapse-panel.component.css'],
  host: {
    '[class.ant-collapse-no-arrow]': '!bpsShowArrow',
    '[class.ant-collapse-item-active]': 'bpsActive',
    '[class.ant-collapse-item-disabled]': 'bpsDisabled'
  }
})
export class BpsCollapsePanelComponent implements OnInit, OnDestroy {
  @Input() @InputBoolean() bpsActive = false;
  @Input() @InputBoolean() bpsDisabled = false;
  @Input() bpsValid = null;
  @Input() @WithConfig(NZ_CONFIG_COMPONENT_NAME, true) @InputBoolean() bpsShowArrow: boolean;
  @Input() bpsExtra: string | TemplateRef<void>;
  @Input() bpsHeader: string | TemplateRef<void>;
  @Input() bpsExpandedIcon: string | TemplateRef<void>;
  @Output() readonly bpsActiveChange = new EventEmitter<boolean>();

  clickHeader(): void {
    if (!this.bpsDisabled) {
      this.bpsCollapseComponent.click(this);
    }
  }

  markForCheck(): void {
    this.cdr.markForCheck();
  }

  constructor(
    public nzConfigService: NzConfigService,
    private cdr: ChangeDetectorRef,
    @Host() private bpsCollapseComponent: BpsCollapseComponent,
    elementRef: ElementRef,
    renderer: Renderer2
  ) {
    renderer.addClass(elementRef.nativeElement, 'ant-collapse-item');
  }

  ngOnInit(): void {
    this.bpsCollapseComponent.addPanel(this);
  }

  ngOnDestroy(): void {
    this.bpsCollapseComponent.removePanel(this);
  }
}
