import {
  ComponentFactory,
  ComponentFactoryResolver,
  Directive,
  ElementRef,
  Host,
  Input,
  Optional,
  Renderer2,
  ViewContainerRef
} from '@angular/core';

import { NzNoAnimationDirective, NzTSType, InputBoolean } from 'ng-zorro-antd/core';

import { BpsToolTipComponent } from './bps-tooltip.component';
import { NzTooltipBaseDirective } from '../bps-popover/base/nz-tooltip-base.directive';
import { NzTooltipTrigger, NzTooltipBaseComponentLegacy } from 'ng-zorro-antd';
import { BpsPopoverType } from '../bps-popover/bps-popover.directive';

@Directive({
  selector: '[bps-tooltip]',
  exportAs: 'bpsTooltip',
  host: {
    '[class.ant-tooltip-open]': 'isTooltipComponentVisible',
    '[class.bps-tooltip-disabled]': 'tooltipDisabled'
  }
})
export class BpsTooltipDirective extends NzTooltipBaseDirective {
  /**
   * The title that should have highest priority.
   */
  @Input('bpsTooltipTitle') specificTitle: NzTSType;

  /**
   * Use the directive's name as the title that have priority in the second place.
   */
  @Input('bps-tooltip') directiveNameTitle: NzTSType | null;

  @Input('bpsTooltipTrigger') specificTrigger: NzTooltipTrigger;
  @Input('bpsTooltipPlacement') specificPlacement: string;
  @Input('bpsTooltipType') popoverType: BpsPopoverType = 'variation_8a';
  @Input('bpsTooltipDisabled') @InputBoolean() tooltipDisabled = false;

  componentFactory: ComponentFactory<BpsToolTipComponent> = this.resolver.resolveComponentFactory(BpsToolTipComponent);

  constructor(
    elementRef: ElementRef,
    hostView: ViewContainerRef,
    resolver: ComponentFactoryResolver,
    renderer: Renderer2,
    @Optional() _tooltip?: NzTooltipBaseComponentLegacy,
    @Host() @Optional() noAnimation?: NzNoAnimationDirective
  ) {
    super(elementRef, hostView, resolver, renderer, _tooltip, noAnimation);
  }
}
