import {
  forwardRef,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ElementRef,
  Renderer2,
  ViewEncapsulation,
  Input
} from '@angular/core';

import { FocusMonitor } from '@angular/cdk/a11y';
import { NG_VALUE_ACCESSOR } from '@angular/forms';
import { BpsRadioComponent } from './bps-radio.component';

@Component({
  selector: '[bps-radio-button]',
  exportAs: 'bpsRadioButton',
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => BpsRadioComponent),
      multi: true
    },
    {
      provide: BpsRadioComponent,
      useExisting: forwardRef(() => BpsRadioButtonComponent)
    }
  ],
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush,
  preserveWhitespaces: false,
  templateUrl: './bps-radio-button.component.html',
  host: {
    '[class.ant-radio-button-wrapper-checked]': 'checked',
    '[class.ant-radio-button-wrapper-disabled]': 'bpsDisabled'
  },
  styleUrls: ['./bps-radio-button.component.css']
})
export class BpsRadioButtonComponent extends BpsRadioComponent {

  /* tslint:disable-next-line:no-any */
  constructor(elementRef: ElementRef, renderer: Renderer2, cdr: ChangeDetectorRef, focusMonitor: FocusMonitor) {
    super(elementRef, renderer, cdr, focusMonitor);
    renderer.removeClass(elementRef.nativeElement, 'ant-radio-wrapper');
    renderer.addClass(elementRef.nativeElement, 'ant-radio-button-wrapper');
  }
}
