import { CdkConnectedOverlay, CdkOverlayOrigin, ConnectedOverlayPositionChange } from '@angular/cdk/overlay';
import { Platform } from '@angular/cdk/platform';
import {
  forwardRef,
  AfterContentInit,
  AfterViewInit,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ContentChildren,
  ElementRef,
  EventEmitter,
  Host,
  Input,
  OnDestroy,
  OnInit,
  Optional,
  Output,
  QueryList,
  Renderer2,
  TemplateRef,
  ViewChild,
  ViewEncapsulation
} from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';
import { merge, EMPTY, Subject } from 'rxjs';
import { flatMap, startWith, takeUntil } from 'rxjs/operators';

import {
  isNotNil,
  slideMotion,
  toBoolean,
  InputBoolean,
  NzNoAnimationDirective,
  NzSizeLDSType
} from 'ng-zorro-antd/core';

import { BpsOptionGroupComponent } from './bps-option-group.component';
import { BpsOptionComponent } from './bps-option.component';
import { TFilterOption } from './bps-option.pipe';
import { BpsSelectTopControlComponent } from './bps-select-top-control.component';
import { BpsSelectService } from './bps-select.service';

@Component({
  selector: 'bps-select',
  exportAs: 'bpsSelect',
  preserveWhitespaces: false,
  providers: [
    BpsSelectService,
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => BpsSelectComponent),
      multi: true
    }
  ],
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  animations: [slideMotion],
  templateUrl: './bps-select.component.html',
  styleUrls: ['./bps-select.component.css'],
  host: {
    '[class.ant-select-lg]': 'bpsSize==="large"',
    '[class.ant-select-sm]': 'bpsSize==="small"',
    '[class.ant-select-enabled]': '!bpsDisabled',
    '[class.ant-select-no-arrow]': '!bpsShowArrow',
    '[class.ant-select-disabled]': 'bpsDisabled',
    '[class.ant-select-allow-clear]': 'bpsAllowClear',
    '[class.ant-select-open]': 'open',
    '(click)': 'toggleDropDown()'
  },
  styles: [
    `
      .ant-select-dropdown {
        top: 100%;
        left: 0;
        position: relative;
        width: 100%;
        margin-top: 4px;
        margin-bottom: 4px;
      }
    `
  ]
})
export class BpsSelectComponent implements ControlValueAccessor, OnInit, AfterViewInit, OnDestroy, AfterContentInit {
  open = false;
  // tslint:disable-next-line:no-any
  value: any | any[];
  onChange: (value: string | string[]) => void = () => null;
  onTouched: () => void = () => null;
  dropDownPosition: 'top' | 'center' | 'bottom' = 'bottom';
  triggerWidth: number;
  private _disabled = false;
  private isInit = false;
  private destroy$ = new Subject();
  @ViewChild(CdkOverlayOrigin, { static: false }) cdkOverlayOrigin: CdkOverlayOrigin;
  @ViewChild(CdkConnectedOverlay, { static: false }) cdkConnectedOverlay: CdkConnectedOverlay;
  @ViewChild(BpsSelectTopControlComponent, { static: true }) bpsSelectTopControlComponent: BpsSelectTopControlComponent;
  @ViewChild(BpsSelectTopControlComponent, { static: true, read: ElementRef }) bpsSelectTopControlElement: ElementRef;
  /** should move to nz-option-container when https://github.com/angular/angular/issues/20810 resolved **/
  @ContentChildren(BpsOptionComponent) listOfNzOptionComponent: QueryList<BpsOptionComponent>;
  @ContentChildren(BpsOptionGroupComponent) listOfNzOptionGroupComponent: QueryList<BpsOptionGroupComponent>;
  @Output() readonly bpsOnSearch = new EventEmitter<string>();
  @Output() readonly bpsScrollToBottom = new EventEmitter<void>();
  @Output() readonly bpsOpenChange = new EventEmitter<boolean>();
  @Output() readonly bpsBlur = new EventEmitter<void>();
  @Output() readonly bpsFocus = new EventEmitter<void>();
  @Input() bpsSize: NzSizeLDSType = 'default';
  @Input() bpsDropdownClassName: string;
  @Input() bpsDropdownMatchSelectWidth = true;
  @Input() bpsDropdownStyle: { [key: string]: string };
  @Input() bpsNotFoundContent: string;
  @Input() @InputBoolean() bpsAllowClear = false;
  @Input() @InputBoolean() bpsShowSearch = false;
  @Input() @InputBoolean() bpsLoading = false;
  @Input() @InputBoolean() bpsAutoFocus = false;
  @Input() bpsPlaceHolder: string;
  @Input() bpsMaxTagCount: number;
  @Input() bpsDropdownRender: TemplateRef<void>;
  @Input() bpsCustomTemplate: TemplateRef<{ $implicit: BpsOptionComponent }>;
  @Input() bpsSuffixIcon: TemplateRef<void>;
  @Input() bpsClearIcon: TemplateRef<void>;
  @Input() bpsRemoveIcon: TemplateRef<void>;
  @Input() bpsMenuItemSelectedIcon: TemplateRef<void>;
  @Input() bpsShowArrow = true;
  @Input() bpsTokenSeparators: string[] = [];
  // tslint:disable-next-line:no-any
  @Input() bpsMaxTagPlaceholder: TemplateRef<{ $implicit: any[] }>;

  @Input()
  set bpsAutoClearSearchValue(value: boolean) {
    this.nzSelectService.autoClearSearchValue = toBoolean(value);
  }

  @Input()
  set bpsMaxMultipleCount(value: number) {
    this.nzSelectService.maxMultipleCount = value;
  }

  @Input()
  set bpsServerSearch(value: boolean) {
    this.nzSelectService.serverSearch = toBoolean(value);
  }

  @Input()
  set bpsMode(value: 'default' | 'multiple' | 'tags') {
    this.nzSelectService.mode = value;
    this.nzSelectService.check();
  }

  @Input()
  set bpsFilterOption(value: TFilterOption) {
    this.nzSelectService.filterOption = value;
  }

  @Input()
  // tslint:disable-next-line:no-any
  set compareWith(value: (o1: any, o2: any) => boolean) {
    this.nzSelectService.compareWith = value;
  }

  @Input()
  set bpsOpen(value: boolean) {
    this.open = value;
    this.nzSelectService.setOpenState(value);
  }

  @Input()
  set bpsDisabled(value: boolean) {
    this._disabled = toBoolean(value);
    this.nzSelectService.disabled = this._disabled;
    this.nzSelectService.check();
    if (this.bpsDisabled && this.isInit) {
      this.closeDropDown();
    }
  }

  get bpsDisabled(): boolean {
    return this._disabled;
  }

  get bpsSelectTopControlDOM(): HTMLElement {
    return this.bpsSelectTopControlElement && this.bpsSelectTopControlElement.nativeElement;
  }

  updateAutoFocus(): void {
    if (this.bpsSelectTopControlDOM && this.bpsAutoFocus) {
      this.bpsSelectTopControlDOM.focus();
    }
  }

  focus(): void {
    if (this.bpsSelectTopControlDOM) {
      this.bpsSelectTopControlDOM.focus();
    }
  }

  blur(): void {
    if (this.bpsSelectTopControlDOM) {
      this.bpsSelectTopControlDOM.blur();
    }
  }

  onFocus(): void {
    this.bpsFocus.emit();
  }

  onBlur(): void {
    this.bpsBlur.emit();
  }

  onKeyDown(event: KeyboardEvent): void {
    this.nzSelectService.onKeyDown(event);
  }

  toggleDropDown(): void {
    if (!this.bpsDisabled) {
      this.nzSelectService.setOpenState(!this.open);
    }
  }

  closeDropDown(): void {
    this.nzSelectService.setOpenState(false);
  }

  onPositionChange(position: ConnectedOverlayPositionChange): void {
    this.dropDownPosition = position.connectionPair.originY;
  }

  updateCdkConnectedOverlayStatus(): void {
    if (this.platform.isBrowser) {
      this.triggerWidth = this.cdkOverlayOrigin.elementRef.nativeElement.getBoundingClientRect().width;
    }
  }

  updateCdkConnectedOverlayPositions(): void {
    setTimeout(() => {
      if (this.cdkConnectedOverlay && this.cdkConnectedOverlay.overlayRef) {
        this.cdkConnectedOverlay.overlayRef.updatePosition();
      }
    });
  }

  constructor(
    renderer: Renderer2,
    public nzSelectService: BpsSelectService,
    private cdr: ChangeDetectorRef,
    private platform: Platform,
    elementRef: ElementRef,
    @Host() @Optional() public noAnimation?: NzNoAnimationDirective
  ) {
    renderer.addClass(elementRef.nativeElement, 'ant-select');
  }

  /** update ngModel -> update listOfSelectedValue **/
  // tslint:disable-next-line:no-any
  writeValue(value: any | any[]): void {
    this.value = value;
    let listValue: any[] = []; // tslint:disable-line:no-any
    if (isNotNil(value)) {
      if (this.nzSelectService.isMultipleOrTags) {
        listValue = value;
      } else {
        listValue = [value];
      }
    }
    this.nzSelectService.updateListOfSelectedValue(listValue, false);
    this.cdr.markForCheck();
  }

  registerOnChange(fn: (value: string | string[]) => void): void {
    this.onChange = fn;
  }

  registerOnTouched(fn: () => void): void {
    this.onTouched = fn;
  }

  setDisabledState(isDisabled: boolean): void {
    this.bpsDisabled = isDisabled;
    this.cdr.markForCheck();
  }

  ngOnInit(): void {
    this.nzSelectService.animationEvent$
      .pipe(takeUntil(this.destroy$))
      .subscribe(() => this.updateCdkConnectedOverlayPositions());
    this.nzSelectService.searchValue$.pipe(takeUntil(this.destroy$)).subscribe(data => {
      this.bpsOnSearch.emit(data);
      this.updateCdkConnectedOverlayPositions();
    });
    this.nzSelectService.modelChange$.pipe(takeUntil(this.destroy$)).subscribe(modelValue => {
      if (this.value !== modelValue) {
        this.value = modelValue;
        this.onChange(this.value);
      }
    });
    this.nzSelectService.open$.pipe(takeUntil(this.destroy$)).subscribe(value => {
      if (this.open !== value) {
        this.bpsOpenChange.emit(value);
      }
      if (value) {
        this.focus();
        this.updateCdkConnectedOverlayStatus();
      } else {
        this.blur();
        this.onTouched();
      }
      this.open = value;
      this.nzSelectService.clearInput();
    });
    this.nzSelectService.check$.pipe(takeUntil(this.destroy$)).subscribe(() => {
      this.cdr.markForCheck();
    });
  }

  ngAfterViewInit(): void {
    this.updateCdkConnectedOverlayStatus();
    this.updateAutoFocus();
    this.isInit = true;
  }

  ngAfterContentInit(): void {
    this.listOfNzOptionGroupComponent.changes
      .pipe(
        startWith(true),
        flatMap(() =>
          merge(
            this.listOfNzOptionGroupComponent.changes,
            this.listOfNzOptionComponent.changes,
            ...this.listOfNzOptionComponent.map(option => option.changes),
            ...this.listOfNzOptionGroupComponent.map(group =>
              group.listOfNzOptionComponent ? group.listOfNzOptionComponent.changes : EMPTY
            )
          ).pipe(startWith(true))
        )
      )
      .subscribe(() => {
        this.nzSelectService.updateTemplateOption(
          this.listOfNzOptionComponent.toArray(),
          this.listOfNzOptionGroupComponent.toArray()
        );
      });
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
  }
}
