import {
  forwardRef,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ContentChild,
  EventEmitter,
  Host,
  Input,
  OnChanges,
  OnDestroy,
  OnInit,
  Optional,
  Output,
  SimpleChange,
  SkipSelf,
  TemplateRef
} from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';
import { Observable, ReplaySubject, Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';

import {
  isNotNil,
  warnDeprecation,
  InputBoolean,
  NzConfigService,
  NzFormatBeforeDropEvent,
  NzFormatEmitEvent,
  NzNoAnimationDirective,
  NzTreeBase,
  NzTreeBaseService,
  NzTreeHigherOrderServiceToken,
  NzTreeNode,
  WithConfig
} from 'ng-zorro-antd/core';
import { NzTreeService } from 'ng-zorro-antd';

export function NzTreeServiceFactory(
  higherOrderService: NzTreeBaseService,
  treeService: NzTreeService
): NzTreeBaseService {
  return higherOrderService ? higherOrderService : treeService;
}

const NZ_CONFIG_COMPONENT_NAME = 'tree';

@Component({
  selector: 'bps-tree',
  exportAs: 'bpsTree',
  templateUrl: './bps-tree.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
  providers: [
    NzTreeService,
    {
      provide: NzTreeBaseService,
      useFactory: NzTreeServiceFactory,
      deps: [[new SkipSelf(), new Optional(), NzTreeHigherOrderServiceToken], NzTreeService]
    },
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => BpsTreeComponent),
      multi: true
    }
  ],
  styleUrls: ['./bps-tree.component.css']
})
export class BpsTreeComponent extends NzTreeBase implements OnInit, OnDestroy, ControlValueAccessor, OnChanges {
  @Input() @InputBoolean() @WithConfig(NZ_CONFIG_COMPONENT_NAME, false) bpsShowIcon: boolean;
  @Input() @InputBoolean() bpsShowExpand: boolean = true;
  @Input() @InputBoolean() bpsShowLine = false;
  @Input() bpsExpandedIcon: TemplateRef<{ $implicit: NzTreeNode }>;
  @Input() @InputBoolean() bpsCheckable = false;
  @Input() @InputBoolean() bpsAsyncData = false;
  @Input() @InputBoolean() bpsDraggable: boolean = false;

  @Input() @InputBoolean() @WithConfig(NZ_CONFIG_COMPONENT_NAME, false) bpsHideUnMatched: boolean;
  @Input() @InputBoolean() bpsSelectMode = false;
  @Input() @InputBoolean() bpsCheckStrictly = false;
  @Input() @WithConfig(NZ_CONFIG_COMPONENT_NAME, false) @InputBoolean() bpsBlockNode: boolean;
  @Input() @InputBoolean() bpsExpandAll = false;

  @Input() @InputBoolean() bpsCustomTree = true;

  @Input() bpsTreeTemplate: TemplateRef<{ $implicit: NzTreeNode }>;
  @ContentChild('bpsTreeTemplate', { static: true }) bpsTreeTemplateChild: TemplateRef<{ $implicit: NzTreeNode }>;
  get treeTemplate(): TemplateRef<{ $implicit: NzTreeNode }> {
    return this.bpsTreeTemplate || this.bpsTreeTemplateChild;
  }

  /**
   * @deprecated 9.0.0 use `bpsExpandAll` instead.
   */
  @Input()
  @InputBoolean()
  set bpsDefaultExpandAll(value: boolean) {
    warnDeprecation(`'bpsDefaultExpandAll' would be removed in 9.0.0. Please use 'bpsExpandAll' instead.`);
    this.bpsExpandAll = value;
    this._bpsDefaultExpandAll = value;
  }

  get bpsDefaultExpandAll(): boolean {
    return this._bpsDefaultExpandAll;
  }

  private _bpsDefaultExpandAll = false;

  @Input() bpsBeforeDrop: (confirm: NzFormatBeforeDropEvent) => Observable<boolean>;

  @Input() @InputBoolean() bpsMultiple = false;

  @Input()
  // tslint:disable-next-line:no-any
  set bpsData(value: any[]) {
    this.initNzData(value);
  }

  /**
   * @deprecated 9.0.0 - use `bpsExpandedKeys` instead.
   */
  @Input()
  set bpsDefaultExpandedKeys(value: string[]) {
    warnDeprecation(`'bpsDefaultExpandedKeys' would be removed in 9.0.0. Please use 'bpsExpandedKeys' instead.`);
    this.bpsDefaultSubject.next({ type: 'nzExpandedKeys', keys: value });
  }

  /**
   * @deprecated 9.0.0 - use `bpsSelectedKeys` instead.
   */
  @Input()
  set bpsDefaultSelectedKeys(value: string[]) {
    warnDeprecation(`'bpsDefaultSelectedKeys' would be removed in 9.0.0. Please use 'bpsSelectedKeys' instead.`);
    this.bpsDefaultSubject.next({ type: 'nzSelectedKeys', keys: value });
  }

  /**
   * @deprecated 9.0.0 - use `bpsCheckedKeys` instead.
   */
  @Input()
  set bpsDefaultCheckedKeys(value: string[]) {
    warnDeprecation(`'bpsDefaultCheckedKeys' would be removed in 9.0.0. Please use 'bpsCheckedKeys' instead.`);
    this.bpsDefaultSubject.next({ type: 'nzCheckedKeys', keys: value });
  }

  @Input()
  set bpsExpandedKeys(value: string[]) {
    this.bpsDefaultSubject.next({ type: 'nzExpandedKeys', keys: value });
  }

  @Input()
  set bpsSelectedKeys(value: string[]) {
    this.bpsDefaultSubject.next({ type: 'nzSelectedKeys', keys: value });
  }

  @Input()
  set bpsCheckedKeys(value: string[]) {
    this.bpsDefaultSubject.next({ type: 'nzCheckedKeys', keys: value });
  }

  @Input()
  set bpsSearchValue(value: string) {
    this._searchValue = value;
    this.nzTreeService.searchExpand(value);
    if (isNotNil(value)) {
      this.bpsSearchValueChange.emit(this.nzTreeService.formatEvent('search', null, null));
      /**
       * @deprecated 9.0.0 - use `nzOnSearchNode` instead.
       * Hide warning, need remove next version
       */
      this.bpsOnSearchNode.emit(this.nzTreeService.formatEvent('search', null, null));
    }
  }

  get bpsSearchValue(): string {
    return this._searchValue;
  }

  /**
   * To render nodes if root is changed.
   */
  get bpsNodes(): NzTreeNode[] {
    return this.nzTreeService.rootNodes;
  }

  @Output() readonly bpsExpandedKeysChange: EventEmitter<string[]> = new EventEmitter<string[]>();
  @Output() readonly bpsSelectedKeysChange: EventEmitter<string[]> = new EventEmitter<string[]>();
  @Output() readonly bpsCheckedKeysChange: EventEmitter<string[]> = new EventEmitter<string[]>();

  @Output() readonly bpsSearchValueChange = new EventEmitter<NzFormatEmitEvent>();

  /**
   * @deprecated use `nzSearchValueChange` instead.
   */
  @Output() readonly bpsOnSearchNode = new EventEmitter<NzFormatEmitEvent>();

  @Output() readonly bpsClick = new EventEmitter<NzFormatEmitEvent>();
  @Output() readonly bpsDblClick = new EventEmitter<NzFormatEmitEvent>();
  @Output() readonly bpsContextMenu = new EventEmitter<NzFormatEmitEvent>();
  @Output() readonly bpsCheckBoxChange = new EventEmitter<NzFormatEmitEvent>();
  @Output() readonly bpsExpandChange = new EventEmitter<NzFormatEmitEvent>();

  @Output() readonly bpsOnDragStart = new EventEmitter<NzFormatEmitEvent>();
  @Output() readonly bpsOnDragEnter = new EventEmitter<NzFormatEmitEvent>();
  @Output() readonly bpsOnDragOver = new EventEmitter<NzFormatEmitEvent>();
  @Output() readonly bpsOnDragLeave = new EventEmitter<NzFormatEmitEvent>();
  @Output() readonly bpsOnDrop = new EventEmitter<NzFormatEmitEvent>();
  @Output() readonly bpsOnDragEnd = new EventEmitter<NzFormatEmitEvent>();

  _searchValue: string;
  bpsDefaultSubject = new ReplaySubject<{ type: string; keys: string[] }>(6);
  destroy$ = new Subject();
  prefixCls = 'ant-tree';
  classMap = {};

  onChange: (value: NzTreeNode[]) => void = () => null;
  onTouched: () => void = () => null;

  setClassMap(): void {
    this.classMap = {
      [this.prefixCls]: true,
      ['bps-tree']: true,
      [this.prefixCls + '-show-line']: this.bpsShowLine,
      [`${this.prefixCls}-icon-hide`]: !this.bpsShowIcon,
      [`${this.prefixCls}-block-node`]: this.bpsBlockNode,
      ['draggable-tree']: this.bpsDraggable,
      ['ant-select-tree']: this.bpsSelectMode
    };
  }

  writeValue(value: NzTreeNode[]): void {
    this.initNzData(value);
  }

  registerOnChange(fn: (_: NzTreeNode[]) => void): void {
    this.onChange = fn;
  }

  registerOnTouched(fn: () => void): void {
    this.onTouched = fn;
  }

  // tslint:disable-next-line:no-any
  initNzData(value: any[]): void {
    if (Array.isArray(value)) {
      this.nzTreeService.isCheckStrictly = this.bpsCheckStrictly;
      this.nzTreeService.isMultiple = this.bpsMultiple;
      this.nzTreeService.initTree(this.coerceTreeNodes(value));
    }
  }

  constructor(
    nzTreeService: NzTreeBaseService,
    public nzConfigService: NzConfigService,
    private cdr: ChangeDetectorRef,
    @Host() @Optional() public noAnimation?: NzNoAnimationDirective
  ) {
    super(nzTreeService);
  }

  ngOnInit(): void {
    this.setClassMap();
    this.bpsDefaultSubject.pipe(takeUntil(this.destroy$)).subscribe((data: { type: string; keys: string[] }) => {
      if (!data || !data.keys) {
        return;
      }
      switch (data.type) {
        case 'nzExpandedKeys':
          this.nzTreeService.calcExpandedKeys(data.keys, this.bpsNodes);
          this.bpsExpandedKeysChange.emit(data.keys);
          break;
        case 'nzSelectedKeys':
          this.nzTreeService.calcSelectedKeys(data.keys, this.bpsNodes, this.bpsMultiple);
          this.bpsSelectedKeysChange.emit(data.keys);
          break;
        case 'nzCheckedKeys':
          this.nzTreeService.calcCheckedKeys(data.keys, this.bpsNodes, this.bpsCheckStrictly);
          this.bpsCheckedKeysChange.emit(data.keys);
          break;
      }
      this.cdr.markForCheck();
    });
    this.nzTreeService
      .eventTriggerChanged()
      .pipe(takeUntil(this.destroy$))
      .subscribe(data => {
        switch (data.eventName) {
          case 'expand':
            if (this.bpsCustomTree) {
              const keys = data.keys;
              data.keys = [keys[keys.length - 1]];
              this.nzTreeService.calcExpandedKeys(data.keys, this.bpsNodes);
            }
            this.bpsExpandChange.emit(data);
            break;
          case 'click':
            this.bpsClick.emit(data);
            break;
          case 'check':
            this.bpsCheckBoxChange.emit(data);
            break;
          case 'dblclick':
            this.bpsDblClick.emit(data);
            break;
          case 'contextmenu':
            this.bpsContextMenu.emit(data);
            break;
          // drag drop
          case 'dragstart':
            this.bpsOnDragStart.emit(data);
            break;
          case 'dragenter':
            this.bpsOnDragEnter.emit(data);
            break;
          case 'dragover':
            this.bpsOnDragOver.emit(data);
            break;
          case 'dragleave':
            this.bpsOnDragLeave.emit(data);
            break;
          case 'drop':
            this.bpsOnDrop.emit(data);
            break;
          case 'dragend':
            this.bpsOnDragEnd.emit(data);
            break;
        }
      });
  }

  ngOnChanges(changes: { [propertyName: string]: SimpleChange }): void {
    if (changes.bpsCheckStrictly) {
      this.nzTreeService.isCheckStrictly = this.bpsCheckStrictly;
    }
    if (changes.bpsMultiple) {
      this.nzTreeService.isMultiple = this.bpsMultiple;
    }
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
  }
}
