import {
  AfterContentInit,
  ChangeDetectionStrategy,
  Component,
  ContentChildren,
  Input,
  QueryList,
  TemplateRef,
  ViewEncapsulation
} from '@angular/core';
import { InputBoolean, NgClassType, NzSizeLDSType } from 'ng-zorro-antd/core';
import { BpsInputDirective } from './bps-input.directive';

@Component({
  selector: 'bps-input-group',
  exportAs: 'bpsInputGroup',
  preserveWhitespaces: false,
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush,
  templateUrl: './bps-input-group.component.html',
  host: {
    '[class.ant-input-group-compact]': 'bpsCompact',
    '[class.ant-input-search-enter-button]': 'bpsSearch',
    '[class.ant-input-search]': 'bpsSearch',
    '[class.ant-input-search-sm]': 'isSmallSearch',
    '[class.ant-input-affix-wrapper]': 'isAffixWrapper',
    '[class.ant-input-group-wrapper]': 'isAddOn',
    '[class.ant-input-group]': 'isGroup',
    '[class.ant-input-group-lg]': 'isLargeGroup',
    '[class.ant-input-group-wrapper-lg]': 'isLargeGroupWrapper',
    '[class.ant-input-affix-wrapper-lg]': 'isLargeAffix',
    '[class.ant-input-search-lg]': 'isLargeSearch',
    '[class.ant-input-group-sm]': 'isSmallGroup',
    '[class.ant-input-affix-wrapper-sm]': 'isSmallAffix',
    '[class.ant-input-group-wrapper-sm]': 'isSmallGroupWrapper'
  }
})
export class BpsInputGroupComponent implements AfterContentInit {
  @ContentChildren(BpsInputDirective) listOfNzInputDirective: QueryList<BpsInputDirective>;
  private _size: NzSizeLDSType = 'default';
  @Input() bpsAddOnBeforeIcon: NgClassType;
  @Input() bpsAddOnAfterIcon: NgClassType;
  @Input() bpsPrefixIcon: NgClassType;
  @Input() bpsSuffixIcon: NgClassType;
  @Input() bpsAddOnBefore: string | TemplateRef<void>;
  @Input() bpsAddOnAfter: string | TemplateRef<void>;
  @Input() bpsPrefix: string | TemplateRef<void>;
  @Input() bpsSuffix: string | TemplateRef<void>;
  @Input() @InputBoolean() bpsSearch = false;
  @Input() @InputBoolean() bpsCompact = false;

  @Input() set bpsSize(value: NzSizeLDSType) {
    this._size = value;
    this.updateChildrenInputSize();
  }

  get bpsSize(): NzSizeLDSType {
    return this._size;
  }

  get isLarge(): boolean {
    return this.bpsSize === 'large';
  }

  get isSmall(): boolean {
    return this.bpsSize === 'small';
  }

  get isAffix(): boolean {
    return !!(this.bpsSuffix || this.bpsPrefix || this.bpsPrefixIcon || this.bpsSuffixIcon);
  }

  get isAddOn(): boolean {
    return !!(this.bpsAddOnAfter || this.bpsAddOnBefore || this.bpsAddOnAfterIcon || this.bpsAddOnBeforeIcon);
  }

  get isAffixWrapper(): boolean {
    return this.isAffix && !this.isAddOn;
  }

  get isGroup(): boolean {
    return !this.isAffix && !this.isAddOn;
  }

  get isLargeGroup(): boolean {
    return this.isGroup && this.isLarge;
  }

  get isLargeGroupWrapper(): boolean {
    return this.isAddOn && this.isLarge;
  }

  get isLargeAffix(): boolean {
    return this.isAffixWrapper && this.isLarge;
  }

  get isLargeSearch(): boolean {
    return this.bpsSearch && this.isLarge;
  }

  get isSmallGroup(): boolean {
    return this.isGroup && this.isSmall;
  }

  get isSmallAffix(): boolean {
    return this.isAffixWrapper && this.isSmall;
  }

  get isSmallGroupWrapper(): boolean {
    return this.isAddOn && this.isSmall;
  }

  get isSmallSearch(): boolean {
    return this.bpsSearch && this.isSmall;
  }

  updateChildrenInputSize(): void {
    if (this.listOfNzInputDirective) {
      this.listOfNzInputDirective.forEach(item => (item.bpsSize = this.bpsSize));
    }
  }

  ngAfterContentInit(): void {
    this.updateChildrenInputSize();
  }
}
