import { ChangeDetectionStrategy, Component, Input, ViewEncapsulation } from '@angular/core';

import { InputBoolean, NzConfigService, WithConfig } from 'ng-zorro-antd/core';

import { BpsCollapsePanelComponent } from './bps-collapse-panel.component';

const NZ_CONFIG_COMPONENT_NAME = 'collapse';

@Component({
  selector: 'bps-collapse',
  exportAs: 'bpsCollapse',
  templateUrl: './bps-collapse.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  styles: [
    `
      bps-collapse {
        display: block;
      }
    `
  ],
  styleUrls: ['./bps-collapse.component.css']
})
export class BpsCollapseComponent {
  private listOfNzCollapsePanelComponent: BpsCollapsePanelComponent[] = [];
  @Input() @WithConfig(NZ_CONFIG_COMPONENT_NAME, false) @InputBoolean() bpsAccordion: boolean;
  @Input() @WithConfig(NZ_CONFIG_COMPONENT_NAME, true) @InputBoolean() bpsBordered: boolean;

  constructor(public nzConfigService: NzConfigService) {}

  addPanel(value: BpsCollapsePanelComponent): void {
    this.listOfNzCollapsePanelComponent.push(value);
  }

  removePanel(value: BpsCollapsePanelComponent): void {
    this.listOfNzCollapsePanelComponent.splice(this.listOfNzCollapsePanelComponent.indexOf(value), 1);
  }

  click(collapse: BpsCollapsePanelComponent): void {
    if (this.bpsAccordion && !collapse.bpsActive) {
      this.listOfNzCollapsePanelComponent
        .filter(item => item !== collapse)
        .forEach(item => {
          if (item.bpsActive) {
            item.bpsActive = false;
            item.bpsActiveChange.emit(item.bpsActive);
            item.markForCheck();
          }
        });
    }
    collapse.bpsActive = !collapse.bpsActive;
    collapse.bpsActiveChange.emit(collapse.bpsActive);
  }
}
