import { ContentObserver } from '@angular/cdk/observers';
import {
  AfterContentInit,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ContentChildren,
  ElementRef,
  HostBinding,
  Inject,
  Input,
  NgZone,
  OnChanges,
  OnDestroy,
  OnInit,
  Optional,
  QueryList,
  Renderer2,
  SimpleChanges,
  ViewChild,
  ViewEncapsulation,
  ViewRef,
  HostListener,
  Output,
  EventEmitter
} from '@angular/core';
import { ANIMATION_MODULE_TYPE } from '@angular/platform-browser/animations';

import {
  findFirstNotEmptyNode,
  findLastNotEmptyNode,
  isEmpty,
  InputBoolean,
  NzConfigService,
  NzSizeLDSType,
  NzSizeMap,
  NzUpdateHostClassService,
  NzWaveConfig,
  NzWaveDirective,
  NZ_WAVE_GLOBAL_CONFIG,
  WithConfig
} from 'ng-zorro-antd/core';
import { NzIconDirective } from 'ng-zorro-antd/icon';
import { Subject } from 'rxjs';
import { startWith, takeUntil } from 'rxjs/operators';
import { BpsInputDirective } from '../bps-input/bps-input.directive';

export type NzButtonType = 'primary' | 'dashed' | 'danger' | 'default' | 'link' | 'variation-1' | 'variation-2' | 'variation-3'
  | 'variation-4' | 'variation-5' | 'variation-6' | 'variation-7' | 'variation-8' | 'variation-9' | 'variation-10' | 'variation-11'
  | 'variation-12'  | 'variation-13' | 'variation-14' | 'variation-15' | 'variation-16' | 'variation-17' | 'variation-18' | 'variation-19' | 'variation-20' | 'editable';
export type NzButtonShape = 'circle' | 'round' | null;

const NZ_CONFIG_COMPONENT_NAME = 'button';

@Component({
  selector: '[bps-button]',
  exportAs: 'bpsButton',
  providers: [NzUpdateHostClassService],
  preserveWhitespaces: false,
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  templateUrl: './bps-button.component.html',
  styleUrls: ['./bps-button.component.css'],
  host: {
    '[class.bps-button-editable-selected]': 'isSelected',
    '[class.bps-button-editable-onedition]': 'showEditionMode'
  }
})
export class BpsButtonComponent implements AfterContentInit, OnInit, OnDestroy, OnChanges {
  @Input() @InputBoolean() isSelected = false;
  @Output() isSelectedChange: EventEmitter<any> = new EventEmitter<any>();
  showEditionMode = false;

  @ViewChild('contentElement', { static: true }) contentElement: ElementRef;
  @ContentChildren(NzIconDirective, { read: ElementRef }) listOfIconElement: QueryList<ElementRef>;
  /*@HostBinding('attr.nz-wave') nzWave = new NzWaveDirective(
    this.ngZone,
    this.elementRef,
    this.waveConfig,
    this.animationType
  );*/

  @Input() @InputBoolean() bpsBlock: boolean = false;
  @Input() @InputBoolean() bpsGhost: boolean = false;
  @Input() @InputBoolean() bpsSearch: boolean = false;
  @Input() @InputBoolean() bpsComputed: boolean = false;
  @Input() @InputBoolean() bpsLoading: boolean = false;
  @Input() bpsType: NzButtonType = 'default';
  @Input() bpsValue: string = '';
  @Output() bpsValueChange: EventEmitter<any> = new EventEmitter<any>();
  @Input() bpsShape: NzButtonShape = null;
  @Input() @WithConfig(NZ_CONFIG_COMPONENT_NAME, 'default') bpsSize: NzSizeLDSType;
  @ViewChild(BpsInputDirective, { static: true, read: ElementRef }) inputElement: ElementRef;

  readonly el: HTMLElement = this.elementRef.nativeElement;
  isInDropdown = false;
  private iconElement: HTMLElement;
  private iconOnly = false;
  private destroy$ = new Subject<void>();

  /** temp solution since no method add classMap to host https://github.com/angular/angular/issues/7289 */
  setClassMap(): void {
    const prefixCls = 'ant-btn';
    const sizeMap: NzSizeMap = { large: 'lg', small: 'sm' };
    this.nzUpdateHostClassService.updateHostClass(this.el, {
      [`${prefixCls}-${this.bpsType}`]: this.bpsType,
      [`${prefixCls}-${this.bpsShape}`]: this.bpsShape,
      [`${prefixCls}-${sizeMap[this.bpsSize]}`]: sizeMap[this.bpsSize],
      [`${prefixCls}-loading`]: this.bpsLoading,
      [`${prefixCls}-icon-only`]: this.iconOnly && !this.bpsSearch && !this.isInDropdown,
      [`${prefixCls}-background-ghost`]: this.bpsGhost,
      [`${prefixCls}-block`]: this.bpsBlock,
      [`ant-input-search-button`]: this.bpsSearch
    });
  }

  updateIconDisplay(value: boolean): void {
    if (this.iconElement) {
      this.renderer.setStyle(this.iconElement, 'display', value ? 'none' : 'inline-block');
    }
  }

  checkContent(): void {
    const hasIcon = this.listOfIconElement && this.listOfIconElement.length;
    if (hasIcon) {
      this.moveIcon();
    }
    this.renderer.removeStyle(this.contentElement.nativeElement, 'display');
    /** https://github.com/angular/angular/issues/12530 **/
    if (isEmpty(this.contentElement.nativeElement)) {
      this.renderer.setStyle(this.contentElement.nativeElement, 'display', 'none');
      this.iconOnly = !!hasIcon;
    } else {
      this.renderer.removeStyle(this.contentElement.nativeElement, 'display');
      this.iconOnly = false;
    }
    this.setClassMap();
    this.updateIconDisplay(this.bpsLoading);
    if (!(this.cdr as ViewRef).destroyed) {
      this.cdr.detectChanges();
    }
  }

  moveIcon(): void {
    if (this.listOfIconElement && this.listOfIconElement.length) {
      const firstChildElement = findFirstNotEmptyNode(this.contentElement.nativeElement);
      const lastChildElement = findLastNotEmptyNode(this.contentElement.nativeElement);
      if (firstChildElement && firstChildElement === this.listOfIconElement.first.nativeElement) {
        this.renderer.insertBefore(this.el, firstChildElement, this.contentElement.nativeElement);
        this.iconElement = firstChildElement as HTMLElement;
      } else if (lastChildElement && lastChildElement === this.listOfIconElement.last.nativeElement) {
        this.renderer.appendChild(this.el, lastChildElement);
      }
    }
  }

  preventDefault($event) {
    $event.preventDefault();
    $event.stopImmediatePropagation();
  }

  @HostListener('window:mouseup', ['$event'])
  handleClick(e: MouseEvent): void {
    if (this.bpsType !== 'editable') {
      return;
    }

    if (this.showEditionMode && this.inputElement && this.inputElement.nativeElement !== e.target) {
      this.showEditionMode = false;
      this.bpsValueChange.emit(this.bpsValue);
    }
  }

  endEditMode($event: KeyboardEvent) {
    this.preventDefault($event);
    if ($event.key === ('Enter' || 'enter')) {
      this.showEditionMode = false;
      this.bpsValueChange.emit(this.bpsValue);
    }
  }

  clicks = 0;
  @HostListener('click', ['$event'])
  onClick(event: MouseEvent) {
    if (this.bpsType !== 'editable') {
      return;
    }
    this.clicks++;
    setTimeout(() => {
      if (this.clicks === 1) {
        this.isSelected = !this.isSelected;
        this.isSelectedChange.emit(this.isSelected);
      } 
      this.clicks = 0;
    }, 300);
  }

  startEditionMode($event: Event) {
    $event.stopImmediatePropagation();
    $event.stopPropagation();
    this.showEditionMode = true;
    this.cdr.detectChanges();
    this.inputElement.nativeElement.focus();
    this.inputElement.nativeElement.select();
  }

  constructor(
    private elementRef: ElementRef,
    private cdr: ChangeDetectorRef,
    private renderer: Renderer2,
    private contentObserver: ContentObserver,
    private nzUpdateHostClassService: NzUpdateHostClassService,
    private ngZone: NgZone,
    public nzConfigService: NzConfigService,
    @Optional() @Inject(NZ_WAVE_GLOBAL_CONFIG) private waveConfig: NzWaveConfig,
    @Optional() @Inject(ANIMATION_MODULE_TYPE) private animationType: string
  ) {
    this.renderer.addClass(elementRef.nativeElement, 'ant-btn');
    this.nzConfigService
      .getConfigChangeEventForComponent(NZ_CONFIG_COMPONENT_NAME)
      .pipe(takeUntil(this.destroy$))
      .subscribe(() => {
        this.setClassMap();
        this.cdr.markForCheck();
      });
  }

  ngAfterContentInit(): void {
    if (!this.contentElement) {
      return;
    }
    this.contentObserver
      .observe(this.contentElement)
      .pipe(
        startWith(true),
        takeUntil(this.destroy$)
      )
      .subscribe(() => {
        // https://github.com/NG-ZORRO/ng-zorro-antd/issues/3079
        Promise.resolve().then(() => this.checkContent());
      });
  }

  ngOnInit(): void {
    this.setClassMap();
    //this.nzWave.ngOnInit();
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
    //this.nzWave.ngOnDestroy();
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (
      changes.bpsBlock ||
      changes.bpsGhost ||
      changes.bpsSearch ||
      changes.bpsType ||
      changes.bpsShape ||
      changes.bpsSize ||
      changes.bpsLoading
    ) {
      this.setClassMap();
    }
    if (changes.bpsLoading) {
      this.updateIconDisplay(this.bpsLoading);
    }
    /*if (changes.bpsType && changes.bpsType.currentValue === 'link') {
      this.nzWave.disable();
    } else {
      this.nzWave.enable();
    }*/
  }
}
