import {
  ComponentFactory,
  ComponentFactoryResolver,
  Directive,
  ElementRef,
  Host,
  Input,
  Optional,
  Renderer2,
  ViewContainerRef
} from '@angular/core';

import { NzNoAnimationDirective, NzTSType } from 'ng-zorro-antd/core';
import { NzTooltipTrigger } from 'ng-zorro-antd/tooltip';

import { BpsPopoverComponent } from './bps-popover.component';
import { NzTooltipBaseDirective } from './base/nz-tooltip-base.directive';

export type BpsPopoverType = 'variation_1' | 'variation_2' | 'variation_3' | 'variation_4' | 'variation_5' | 'variation_6' | 'variation_7a' |
  'variation_7b' | 'variation_8a' | 'variation_8b' | 'variation_9a' | 'variation_9b' | 'variation_10' | 'variation_11';

@Directive({
  selector: '[bps-popover]',
  exportAs: 'bpsPopover',
  host: {
    '[class.ant-popover-open]': 'isTooltipComponentVisible'
  }
})
export class BpsPopoverDirective extends NzTooltipBaseDirective {
  @Input('bpsPopoverTitle') specificTitle: NzTSType;
  @Input('bpsPopoverContent') specificContent: NzTSType;
  @Input('bps-popover') directiveNameTitle: NzTSType | null;
  @Input('bpsPopoverTrigger') specificTrigger: NzTooltipTrigger;
  @Input('bpsPopoverPlacement') specificPlacement: string;
  @Input('bpsPopoverType') popoverType: BpsPopoverType = 'variation_1';

  componentFactory: ComponentFactory<BpsPopoverComponent> = this.resolver.resolveComponentFactory(BpsPopoverComponent);

  constructor(
    elementRef: ElementRef,
    hostView: ViewContainerRef,
    resolver: ComponentFactoryResolver,
    renderer: Renderer2,
    @Optional() tooltip: BpsPopoverComponent,
    @Host() @Optional() public noAnimation?: NzNoAnimationDirective
  ) {
    super(elementRef, hostView, resolver, renderer, tooltip, noAnimation);
  }
}
