import { Pipe, PipeTransform, QueryList } from '@angular/core';
import { BpsOptionGroupComponent } from './bps-option-group.component';
import { BpsOptionComponent } from './bps-option.component';

export type TFilterOption = (input: string, option: BpsOptionComponent) => boolean;

@Pipe({ name: 'bpsFilterOption' })
export class BpsFilterOptionPipe implements PipeTransform {
  transform(
    options: BpsOptionComponent[] | QueryList<BpsOptionComponent>,
    searchValue: string,
    filterOption: TFilterOption,
    serverSearch: boolean
  ): BpsOptionComponent[] {
    if (serverSearch || !searchValue) {
      return options as BpsOptionComponent[];
    } else {
      return (options as BpsOptionComponent[]).filter(o => filterOption(searchValue, o));
    }
  }
}

@Pipe({ name: 'bpsFilterGroupOption' })
export class BpsFilterGroupOptionPipe implements PipeTransform {
  transform(
    groups: BpsOptionGroupComponent[],
    searchValue: string,
    filterOption: TFilterOption,
    serverSearch: boolean
  ): BpsOptionGroupComponent[] {
    if (serverSearch || !searchValue) {
      return groups;
    } else {
      return (groups as BpsOptionGroupComponent[]).filter(g => {
        return g.listOfNzOptionComponent.some(o => filterOption(searchValue, o));
      });
    }
  }
}

export function defaultFilterOption(searchValue: string, option: BpsOptionComponent): boolean {
  if (option && option.bpsLabel) {
    return option.bpsLabel.toLowerCase().indexOf(searchValue.toLowerCase()) > -1;
  } else {
    return false;
  }
}
