import {
  AfterViewInit,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ElementRef,
  EventEmitter,
  Input,
  NgZone,
  OnDestroy,
  OnInit,
  Output,
  QueryList,
  TemplateRef,
  ViewChild,
  ViewChildren,
  ViewEncapsulation
} from '@angular/core';
import { fromEvent, Subject } from 'rxjs';
import { filter, map, pairwise, takeUntil } from 'rxjs/operators';
import { BpsOptionGroupComponent } from './bps-option-group.component';
import { BpsOptionLiComponent } from './bps-option-li.component';
import { BpsOptionComponent } from './bps-option.component';
import { BpsSelectService } from './bps-select.service';

@Component({
  selector: '[bps-option-container]',
  exportAs: 'bpsOptionContainer',
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  preserveWhitespaces: false,
  templateUrl: './bps-option-container.component.html'
})
export class BpsOptionContainerComponent implements OnDestroy, OnInit, AfterViewInit {
  private destroy$ = new Subject();
  private lastScrollTop = 0;
  @ViewChildren(BpsOptionLiComponent) listOfNzOptionLiComponent: QueryList<BpsOptionLiComponent>;
  @ViewChild('dropdownUl', { static: true }) dropdownUl: ElementRef<HTMLUListElement>;
  @Input() bpsNotFoundContent: string;
  @Input() bpsMenuItemSelectedIcon: TemplateRef<void>;
  @Output() readonly bpsScrollToBottom = new EventEmitter<void>();

  scrollIntoViewIfNeeded(option: BpsOptionComponent): void {
    // delay after open
    setTimeout(() => {
      if (this.listOfNzOptionLiComponent && this.listOfNzOptionLiComponent.length && option) {
        const targetOption = this.listOfNzOptionLiComponent.find(o =>
          this.nzSelectService.compareWith(o.bpsOption.bpsValue, option.bpsValue)
        );
        // tslint:disable:no-any
        if (targetOption && targetOption.el && (targetOption.el as any).scrollIntoViewIfNeeded) {
          (targetOption.el as any).scrollIntoViewIfNeeded(false);
        }
      }
    });
  }

  trackLabel(_index: number, option: BpsOptionGroupComponent): string | TemplateRef<void> {
    return option.bpsLabel;
  }

  // tslint:disable-next-line:no-any
  trackValue(_index: number, option: BpsOptionComponent): any {
    return option.bpsValue;
  }

  constructor(public nzSelectService: BpsSelectService, private cdr: ChangeDetectorRef, private ngZone: NgZone) {}

  ngOnInit(): void {
    this.nzSelectService.activatedOption$.pipe(takeUntil(this.destroy$)).subscribe(option => {
      this.scrollIntoViewIfNeeded(option!);
    });
    this.nzSelectService.check$.pipe(takeUntil(this.destroy$)).subscribe(() => {
      this.cdr.markForCheck();
    });
    this.ngZone.runOutsideAngular(() => {
      const ul = this.dropdownUl.nativeElement;
      fromEvent<MouseEvent>(ul, 'scroll')
        .pipe(takeUntil(this.destroy$))
        .subscribe(e => {
          e.preventDefault();
          e.stopPropagation();
          if (ul && ul.scrollTop > this.lastScrollTop && ul.scrollHeight < ul.clientHeight + ul.scrollTop + 10) {
            this.lastScrollTop = ul.scrollTop;
            this.ngZone.run(() => {
              this.bpsScrollToBottom.emit();
            });
          }
        });
    });
  }

  ngAfterViewInit(): void {
    this.listOfNzOptionLiComponent.changes
      .pipe(
        map(list => list.length),
        pairwise(),
        filter(([before, after]) => after < before),
        takeUntil(this.destroy$)
      )
      .subscribe(() => (this.lastScrollTop = 0));
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
  }
}
