import {
  ChangeDetectorRef,
  Component,
  EventEmitter,
  Input,
  OnDestroy,
  OnInit,
  Output,
  AfterViewInit,
  TemplateRef,
  ViewChild,
  TrackByFunction,
  OnChanges,
  SimpleChanges,
  ElementRef,
  HostListener,
  ChangeDetectionStrategy,
} from '@angular/core';
import { Subject } from 'rxjs';
import { takeUntil, window } from 'rxjs/operators';

import {InputBoolean, InputNumber, NzSizeMDSType, toBoolean} from 'ng-zorro-antd/core';
import { NzI18nService } from 'ng-zorro-antd/i18n';
import { TableConfig, Field } from '../core/interfaces/grid-config';
import { CeldType } from '../core/enums/celdType.enum';
import { BpsInputDirective } from '../bps-input/bps-input.directive';
import { CheckboxSelect } from '../bps-table/bps-table.component';
import { NzTableComponent } from 'ng-zorro-antd';

@Component({
  // tslint:disable-next-line: component-selector
  selector: 'bps-table-expandable-panel',
  exportAs: 'bpsTableExpandablePanel',
  templateUrl: './bps-table-expandable-panel.component.html',
  styleUrls: ['./bps-table-expandable-panel.component.css'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
// tslint:disable-next-line no-any
export class BpsTableExpandablePanelComponent<T = any> implements OnInit, OnDestroy, AfterViewInit, OnChanges {
  locale: any = {}; // tslint:disable-line:no-any
  private destroy$ = new Subject<void>();
  checkboxCache: CheckboxSelect[] = [];
  mapOfExpandData: { [key: string]: boolean } = {};
  _data = [];
  editId = null;
  isExpanded = false;
  searchBoxHovered = false;
  private _searchSubject: Subject<string> = new Subject();

  /* Table API */ 
  // tslint:disable-next-line: no-input-rename
  @Input()
  set data(data) {
    this._data = data;
    this.updateCheckboxCache();
  }
  @Input() @InputBoolean() frontPagination = false;
  @Input() total = 0;
  @Input() pageIndex = 1;
  @Input() pageSize = 10;
  @Input() @InputBoolean() showPagination = false;
  @Input() paginationPosition: 'top' | 'bottom' | 'both' = 'bottom';
  @Input() @InputBoolean() bordered = false;
  @Input() widthConfig: string[] = [];
  @Input() @InputBoolean() loading = false;
  @Input() loadingDelay = 0;
  @Input() loadingIndicator: TemplateRef<void>;
  @Input() scroll: { x?: string | null; y?: string | null } = { x: null, y: null };
  @Input() title: string | TemplateRef<void>;
  @Input() footer: string | TemplateRef<void>;
  @Input() noResult: string | TemplateRef<void>;
  @Input() pageSizeOptions = [10, 20, 30, 40, 50];
  @Input() @InputBoolean() showQuickJumper = false;
  @Input() @InputBoolean() showSizeChanger = false;
  @Input() showTotal: TemplateRef<{ $implicit: number; range: [number, number] }>;
  @Input() @InputBoolean() hideOnSinglePage = false;
  @Input() @InputBoolean() simple = false;
  @Input() @InputBoolean() virtualScroll = false;
  @Input() @InputNumber() virtualItemSize = 0;
  @Input() @InputNumber() virtualMaxBufferPx = 200;
  @Input() @InputNumber() virtualMinBufferPx = 100;
  @Input() virtualForTrackBy: TrackByFunction<T>;
  @Input() @InputBoolean() inlineEdit = false;
  @Output() pageIndexChange = new EventEmitter<any>();
  @Output() currentPageDataChange = new EventEmitter<any>();
  @Output() queryParamsChange = new EventEmitter<any>();
  @Output() pageSizeChange = new EventEmitter<any>();
  @Output() onclickRow = new EventEmitter<any>();
  @Output() ondblclickRow = new EventEmitter<any>();
  @Output() selectionChange = new EventEmitter<any>();

/* Thead API */
  @Input() singleSort = true;
  @Output() sortChange = new EventEmitter<any>();
  
  /* BPS API */ 
  @Input() config: TableConfig;
  @Output() configChange: EventEmitter<TableConfig> = new EventEmitter<TableConfig>();
  @Input() gridID: string;
  @Output() onedit = new EventEmitter<any>();
  @Input() menuTemplate: TemplateRef<void>;
  @Input() currentPreviewTemplate: TemplateRef<void>;

  @ViewChild(BpsInputDirective, { static: false, read: ElementRef }) inputElement: ElementRef;
  @ViewChild('gridComponent', { read: NzTableComponent, static: false }) gridComponent: NzTableComponent;
  @ViewChild('bpsExpandablePanel', { read: ElementRef, static: false }) panel: ElementRef;

  @HostListener('window:mouseup', ['$event'])
  handleClick(e: MouseEvent): void {
    if (this.editId !== null && this.inputElement && !this.inputElement.nativeElement.isEqualNode(e.target)) {
      this.emitOnEditEvent();
      this.editId = null;
      this.cdr.detectChanges();
    }
  }

  emitOnEditEvent() {
    let editedEl = this._data.filter(el => el[this.config.fieldId] === this.editId);
    if (editedEl.length) {
      this.onedit.emit(editedEl);
    }
  }

  endEditMode($event: KeyboardEvent, index: number, data: any = null) {
    if ($event.key === ('Enter' || 'enter')) {
      this.emitOnEditEvent();
      this.editId = null;
      this.cdr.detectChanges();
    }
  }

  preventDefault($event: MouseEvent) {
    $event.preventDefault();
    $event.stopImmediatePropagation();
  }

  emitBpsEvent($event, type: string) {
    switch (type) {
      case 'pageIndex':
        this.pageIndexChange.emit($event);
        break;
      case 'currentPageData':
        this.currentPageDataChange.emit($event);
        break;
      case 'queryParams':
        this.queryParamsChange.emit($event);
        break;
      case 'pageSize':
        this.pageSizeChange.emit($event);
        break;
    }
  }

  getFields(): Field[] {
    return this.config ? this.config.fields.filter(item => item.hidden === undefined || !item.hidden) : [];
  }

  constructor(
    private cdr: ChangeDetectorRef,
    private i18n: NzI18nService
  ) {  }

  ngAfterViewInit() {
    this.cdr.detectChanges();
  }

  ngOnChanges(changes: SimpleChanges) {
    if (changes.data && this.config) {
      this.updateCheckboxCache();
    }
  }

  ngOnInit(): void {
    this.i18n.localeChange.pipe(takeUntil(this.destroy$)).subscribe(() => {
      this.locale = this.i18n.getLocaleData('Table');
      this.cdr.markForCheck();
    });
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
    this._searchSubject.unsubscribe();
  }

  isCeldTypeTemplateRef(field: Field) {
    return field.celdType === CeldType.TemplateRef;
  }

  isCeldTypeDefault(field: Field) {
    return field.celdType === CeldType.Default;
  }

  getTDClassMap(field: Field, data, fi = 9999) {
    return {
      ...field.ngClass,
      ['bps-td-expandable-panel']: true,
      ['bps-td-disabled']: data.disabled,
      ['bps-td-no-padding']: data[this.config.fieldId] === this.editId,
      ['bps-fst-column']: !fi,
    }
  }

  isRowSelected(data: any): boolean {
    if (this.config) {
      const dataSelected = this.checkboxCache.filter(item => item.selected).map(item => item.data[this.config.fieldId]);
      return dataSelected.indexOf(data[this.config.fieldId]) !== -1;
    }
    return false;
  }

  updateCheckboxCache(): void {
    this.checkboxCache.length = 0;
    this._data.forEach(item => {
      this.checkboxCache.push({
        selected: item.selected ? item.selected : false,
        data: item
      });
    });
  }

  clicks = 0;
  clickRow(event: MouseEvent, data: any) {
    event.preventDefault();
    event.stopImmediatePropagation();
    this.clicks++;
    setTimeout(() => {
      if (this.clicks === 1) {
        this.selectRow(data);
      } else if (this.clicks === 2) {
        this.ondblclickRow.emit(data);
      }
      this.clicks = 0;
    }, 300);
  }

  startEdit(data: any, event: MouseEvent): void {
    event.preventDefault();
    event.stopPropagation();
    if (this.inlineEdit && !data.disabled) {
      this.editId = data[this.config.fieldId];
      this.cdr.detectChanges();
      this.inputElement.nativeElement.focus();
      setTimeout(() => {
        this.inputElement.nativeElement.select();
      }, 100);
    }
  }

  selectText($event) {
    $event.target.select();
  }

  selectRow(data: any) {
    if (!data.disabled) {
      this.onclickRow.emit(data);
      this.checkboxCache.forEach(item => {
        if (item.data[this.config.fieldId] === data[this.config.fieldId]) {
          item.selected = true;
          this.selectionChange.emit(item);
        } else {
          item.selected = false;
        }
      });
    }  
  }

}
