import {
  ChangeDetectionStrategy,
  Component,
  ContentChildren,
  Input,
  QueryList,
  TemplateRef,
  ViewEncapsulation
} from '@angular/core';
import { BpsOptionComponent } from './bps-option.component';

@Component({
  selector: 'bps-option-group',
  exportAs: 'bpsOptionGroup',
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush,
  templateUrl: './bps-option-group.component.html'
})
export class BpsOptionGroupComponent {
  isLabelString = false;
  label: string | TemplateRef<void>;
  @ContentChildren(BpsOptionComponent) listOfNzOptionComponent: QueryList<BpsOptionComponent>;

  @Input()
  set bpsLabel(value: string | TemplateRef<void>) {
    this.label = value;
    this.isLabelString = !(this.bpsLabel instanceof TemplateRef);
  }

  get bpsLabel(): string | TemplateRef<void> {
    return this.label;
  }
}
