import { FocusMonitor } from '@angular/cdk/a11y';
import { ENTER, LEFT_ARROW, RIGHT_ARROW, SPACE } from '@angular/cdk/keycodes';
import {
  forwardRef,
  AfterViewInit,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ElementRef,
  Input,
  OnDestroy,
  TemplateRef,
  ViewChild,
  ViewEncapsulation,

  EventEmitter,
  Output
} from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';

import { InputBoolean, NzConfigService, NzSizeDSType, WithConfig } from 'ng-zorro-antd/core';

const NZ_CONFIG_COMPONENT_NAME = 'switch';

@Component({
  selector: 'bps-switch',
  exportAs: 'bpsSwitch',
  preserveWhitespaces: false,
  templateUrl: './bps-switch.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  styleUrls: ['./bps-switch.component.css'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => BpsSwitchComponent),
      multi: true
    }
  ],
  host: {
    '(click)': 'hostClick($event)'
  },
  styles: [
    `
      bps-switch {
        display: inline-block;
      }
    `
  ]
})
export class BpsSwitchComponent implements ControlValueAccessor, AfterViewInit, OnDestroy {
  checked = false;
  onChange: (value: boolean) => void = () => null;
  onTouched: () => void = () => null;
  @ViewChild('switchElement', { static: true }) private switchElement: ElementRef;
  @Input() @InputBoolean() bpsLoading = false;
  @Input() @InputBoolean() bpsDual = false;
  @Input() bpsDualValues: { title: string; checked: boolean }[] = [];
  @Output() bpsDualValuesChange: EventEmitter<{ title: string; checked: boolean }[]> = new EventEmitter();
  @Input() @InputBoolean() bpsDisabled = false;
  @Input() @InputBoolean() bpsControl = false;
  @Input() bpsCheckedChildren: string | TemplateRef<void>;
  @Input() bpsUnCheckedChildren: string | TemplateRef<void>;
  @Input() @WithConfig(NZ_CONFIG_COMPONENT_NAME, 'default') bpsSize: NzSizeDSType;

  hostClick(e: MouseEvent): void {
    e.preventDefault();
    if (!this.bpsDisabled && !this.bpsLoading && !this.bpsControl) {
      this.updateValue(!this.checked);
    }
  }

  checkNode(nodeA: { title: string; checked: boolean }, nodeB: { title: string; checked: boolean }) {
    if (!this.bpsDisabled) {
      nodeA.checked = true;
      nodeB.checked = false;
      this.bpsDualValuesChange.emit(this.bpsDualValues);
    }
  }

  updateValue(value: boolean): void {
    if (this.checked !== value) {
      this.checked = value;
      this.onChange(this.checked);
    }
  }

  onKeyDown(e: KeyboardEvent): void {
    if (!this.bpsControl && !this.bpsDisabled && !this.bpsLoading) {
      if (e.keyCode === LEFT_ARROW) {
        this.updateValue(false);
        e.preventDefault();
      } else if (e.keyCode === RIGHT_ARROW) {
        this.updateValue(true);
        e.preventDefault();
      } else if (e.keyCode === SPACE || e.keyCode === ENTER) {
        this.updateValue(!this.checked);
        e.preventDefault();
      }
    }
  }

  focus(): void {
    if (this.switchElement) {
      this.focusMonitor.focusVia(this.switchElement.nativeElement, 'keyboard');
    }  
  }

  blur(): void {
    if (this.switchElement) {
      this.switchElement.nativeElement.blur();
    } 
  }

  constructor(
    public nzConfigService: NzConfigService,
    private cdr: ChangeDetectorRef,
    private focusMonitor: FocusMonitor
  ) {}

  ngAfterViewInit(): void {
    if (this.switchElement) {
      this.focusMonitor.monitor(this.switchElement.nativeElement, true).subscribe(focusOrigin => {
        if (!focusOrigin) {
          // When a focused element becomes disabled, the browser *immediately* fires a blur event.
          // Angular does not expect events to be raised during change detection, so any state change
          // (such as a form control's 'ng-touched') will cause a changed-after-checked error.
          // See https://github.com/angular/angular/issues/17793. To work around this, we defer
          // telling the form control it has been touched until the next tick.
          Promise.resolve().then(() => this.onTouched());
        }
      });
    }
    this.cdr.detectChanges();
  }

  ngOnDestroy(): void {
    if (this.switchElement) {
      this.focusMonitor.stopMonitoring(this.switchElement.nativeElement);
    }
  }

  writeValue(value: boolean): void {
    this.checked = value;
    this.cdr.markForCheck();
  }

  registerOnChange(fn: (_: boolean) => void): void {
    this.onChange = fn;
  }

  registerOnTouched(fn: () => void): void {
    this.onTouched = fn;
  }

  setDisabledState(isDisabled: boolean): void {
    this.bpsDisabled = isDisabled;
    this.cdr.markForCheck();
  }
}
