import { Component, OnInit, ViewEncapsulation, Output, Input, EventEmitter, OnChanges, SimpleChanges } from '@angular/core';
import { InputBoolean } from 'ng-zorro-antd';

declare var tinymce: any;

@Component({
  exportAs: 'bpsTextEditor',
  selector: 'bps-text-editor',
  templateUrl: './bps-text-editor.component.html',
  styleUrls: ['./bps-text-editor.component.css'],
  encapsulation: ViewEncapsulation.None
})
export class BpsTextEditorComponent implements OnInit, OnChanges {

  showEditor = false;
  @Input() @InputBoolean() lazyLoaded = false;
  @Output() oninit: EventEmitter<any> = new EventEmitter<any>();
  @Output() onchange: EventEmitter<any> = new EventEmitter<any>();
  @Output() onblur: EventEmitter<any> = new EventEmitter<any>();
  @Output() onkeyup: EventEmitter<any> = new EventEmitter<any>();
  @Input() @InputBoolean() disabled = false;
  @Input() height = '250px';
  @Input() editorID = 'myEditor';
  @Input() statusbar = false;
  @Input() resize = false;
  // tslint:disable-next-line: max-line-length
  @Input() toolbarmobile = ['bold', 'italic', 'underline', 'strikethrough', 'alignleft', 'aligncenter', 'alignright', 'alignjustify', 'bullist', 'numlist', 'forecolor'];
  @Input() toolbar = 'bold italic underline forecolor | strikethrough backcolor | alignleft aligncenter alignright alignjustify | bullist numlist image';
  @Input() tinyMceSettings!: any;

  constructor() { }

  ngOnInit() {
    if (!this.lazyLoaded) {
      this.initTinyMCE();
    }
  }

  initTinyMCE() {
    if (this.tinyMceSettings === undefined) {
      this.tinyMceSettings = {
        mobile: {
          theme: 'mobile',
          plugins: ['image table textcolor lists advlist'],
          toolbar: this.toolbarmobile
        },
        menubar: false,
        content_css: '/assets/tiny.css',
        image_title: true,
        toolbar_location: 'bottom',
        resize: this.resize,
        automatic_uploads: true,
        height: this.height,
        statusbar: this.statusbar,
        file_picker_types: 'image',
        images_upload_url: '#',
        setup: (editor: any) => {
          editor.on('init', (obj) => {
            if (this.disabled) {
              this.disableEditor();
            }
            this.oninit.emit(obj);
          });
          editor.on('blur', (obj) => {
            this.onblur.emit(obj);
          });
          editor.on('keyup', (obj) => {
            this.onkeyup.emit(obj);
          });
          editor.on('Change', (obj) => {
            this.onchange.emit(obj);
          });
        },
        color_map: [
          "e94c0a", "Orange",
          "00a2d1", "Blue",
          "7bc053", "Green",
          "d80f0f", "Red",
          "e9d90a", "Yellow",
          "ffffff", "White"
        ],
        plugins: ['image table textcolor lists advlist'],
        toolbar: this.toolbar
      };
    }

    setTimeout(() => {
      this.showEditor = true;
    }, 100);
  }

  disableEditor() {
    tinymce.get(this.editorID).mode.set('readonly');
  }

  enableEditor() {
    tinymce.get(this.editorID).mode.set('design');
  }

  ngOnChanges(changes: SimpleChanges) {
    if (changes.disabled !== null && changes.disabled !== undefined && tinymce.get(this.editorID)) {
      if (changes.disabled) {
        this.disableEditor();
      } else {
        this.enableEditor();
      }
    }

    if (changes.lazyLoaded !== null && changes.lazyLoaded !== undefined) {
      if (!this.lazyLoaded) {
        this.initTinyMCE();
      } else if (tinymce.get(this.editorID)) {
        this.showEditor = false;
        tinymce.get(this.editorID).remove();
      }
    }
  }

}
