import {
  AfterContentInit,
  ContentChildren,
  Directive,
  ElementRef,
  Input,
  OnChanges,
  OnDestroy,
  OnInit,
  QueryList,
  Renderer2,
  SimpleChanges
} from '@angular/core';
import { Subject } from 'rxjs';
import { startWith, takeUntil } from 'rxjs/operators';

import { InputBoolean, NzConfigService, NzUpdateHostClassService, WithConfig } from 'ng-zorro-antd/core';

import { BpsFormLabelComponent } from './bps-form-label.component';

const NZ_CONFIG_COMPONENT_NAME = 'form';

@Directive({
  selector: '[bps-form]',
  exportAs: 'bpsForm',
  providers: [NzUpdateHostClassService]
})
export class BpsFormDirective implements OnInit, OnChanges, AfterContentInit, OnDestroy {
  @Input() bpsLayout = 'horizontal';
  @Input() @WithConfig(NZ_CONFIG_COMPONENT_NAME, false) @InputBoolean() bpsNoColon: boolean;

  @ContentChildren(BpsFormLabelComponent, { descendants: true }) bpsFormLabelComponent: QueryList<BpsFormLabelComponent>;

  destroy$ = new Subject();

  setClassMap(): void {
    this.nzUpdateHostClassService.updateHostClass(this.elementRef.nativeElement, {
      [`ant-form-${this.bpsLayout}`]: this.bpsLayout
    });
  }

  updateItemsDefaultColon(): void {
    if (this.bpsFormLabelComponent) {
      this.bpsFormLabelComponent.forEach(item => item.setDefaultNoColon(this.bpsNoColon));
    }
  }

  constructor(
    public nzConfigService: NzConfigService,
    private elementRef: ElementRef,
    private renderer: Renderer2,
    private nzUpdateHostClassService: NzUpdateHostClassService
  ) {
    this.renderer.addClass(elementRef.nativeElement, 'ant-form');
  }

  ngOnInit(): void {
    this.setClassMap();
  }

  ngOnChanges(changes: SimpleChanges): void {
    this.setClassMap();
    if (changes.hasOwnProperty('bpsNoColon')) {
      this.updateItemsDefaultColon();
    }
  }

  ngAfterContentInit(): void {
    this.bpsFormLabelComponent.changes
      .pipe(
        startWith(null),
        takeUntil(this.destroy$)
      )
      .subscribe(() => {
        this.updateItemsDefaultColon();
      });
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
  }
}
