
import {
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ContentChild,
  Host,
  Input,
  Optional,
  TemplateRef,
  ViewEncapsulation
} from '@angular/core';

import { zoomBigMotion, NzNoAnimationDirective, NzTSType, InputBoolean } from 'ng-zorro-antd/core';
import { NzTooltipBaseComponentLegacy } from 'ng-zorro-antd';
import { BpsPopoverType } from '../bps-popover/bps-popover.directive';

@Component({
  selector: 'bps-tooltip',
  exportAs: 'bpsTooltipComponent',
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  animations: [zoomBigMotion],
  templateUrl: './bps-tooltip.component.html',
  preserveWhitespaces: false,
  providers: [
    {
      provide: NzTooltipBaseComponentLegacy,
      useExisting: BpsToolTipComponent
    }
  ],
  styles: [
    `
      .ant-tooltip {
        position: relative;
      }
    `
  ],
  styleUrls: ['./bps-tooltip.component.css']
})
export class BpsToolTipComponent extends NzTooltipBaseComponentLegacy {

  @Input() bpsPopoverType: BpsPopoverType = 'variation_8a';
  @Input() bpsTitle: NzTSType | null;
  @Input() @InputBoolean() bpsTooltipDisabled = false;
  @ContentChild('nzTemplate', { static: true }) nzTitleTemplate: TemplateRef<void>;

  constructor(cdr: ChangeDetectorRef, @Host() @Optional() public noAnimation?: NzNoAnimationDirective) {
    super(cdr);
  }
}
